	module.exports = {"data":{"markdownRemark":{"html":"<h2>Build Measure Learn</h2>\n<p>Agile methodology is about how to build things right. “Build Measure Learn” is about how to build the right thing.</p>\n<p>The term comes from the <a href=\"https://en.wikipedia.org/wiki/Lean_startup\">Lean Startup methodology</a>, popularised by <a href=\"https://en.wikipedia.org/wiki/Eric_Ries\">Eric Ries</a>, the author of <a href=\"https://www.amazon.co.uk/Lean-Startup-Innovation-Successful-Businesses/dp/0670921602\">The Lean Startup</a>.</p>\n<p><strong>Build Measure Learn</strong> is sometimes referred to as Hypothesis Driven Development. </p>\n<h3>The Premise:</h3>\n<p>The goal of Build Measure Learn is to <em>build</em> a Minimum Viable Product (MVP), <em>measure</em> the impact of the MVP and <em>learn</em> about whether the Product that is being built is viable, valuable and fulfils the assumed success criteria.</p>\n<h4>More Information:</h4>\n<p><a href=\"https://steveblank.com/2015/05/06/build-measure-learn-throw-things-against-the-wall-and-see-if-they-work/\">Why Build, Measure, Learn – isn’t just throwing things against the wall to see if they work – the Minimal Viable Product</a></p>\n<p><a href=\"https://www.mindtools.com/pages/article/build-measure-learn.htm\">The Build-Measure-Learn Feedback Loop</a></p>","fields":{"slug":"/agile/build-measure-learn/"},"frontmatter":{"title":"Build Measure Learn"}}},"pathContext":{"slug":"/agile/build-measure-learn/"}}

